import React,{Component} from 'react';
import Slider from "react-slick";
import * as UI from './style';

export default class Index extends Component{
    // ({setMenu}) 
    constructor(props) {
        super(props)
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
      };

      next() {
        this.slider.slickNext();
      }
      previous() {
        this.slider.slickPrev();
      }
    
    render(){
        return (
            <UI.Wrapper>
                <div className="splash-screen">
                <Slider ref={c => (this.slider = c)} {...this.settings}>
                    <div className="sp splash-1 relative">
                    <div className="px-4 h-100 splash-bg d-flex align-items-center justify-content-center" style={{background:`linear-gradient(to right, rgba(0,0,0,.8), rgba(0,0,0,.8)), url(${require("../../assets/img/card-2.jpg")})`, backgroundSize:"cover", backgroundPosition:"right" }}>
                        <div className="">
                        <div className="logo">
                            <img src={require("../../assets/img/white1.png")} alt=""  className=" mx-auto d-block mb-5"/>
                        </div>
                        <h6 className="heading mb-3">Exchange Giftcards with Ease</h6>
                        <p className="desc mb-4">Trade giftcards at reasonable rates. Instant payment, no stress, no story.</p>
                        <div className="d-flex justify-content-between button-container px-2">
                            <p className=" mb-0 px-4 py-2" onClick={() => this.props.setMenu("home")} style={{background:"#9E1C31", borderRadius:"5px"}}>Get Started</p>
                            <p className="button mb-0 px-5 py-2" onClick={this.next}>Next</p>
                        </div>
                        </div>
                    </div>
                    
                    </div>

                    <div className="sp splash-1 relative">
                    <div className="px-4 h-100 splash-bg d-flex align-items-center justify-content-center" style={{background:`linear-gradient(to right, rgba(0,0,0,.65), rgba(0,0,0,.65)), url(${require("../../assets/img/bitcoin-3.jpg")})`, backgroundSize:"cover", backgroundPosition:"left" }}>
                        <div className="">
                        <div className="logo">
                            <img src={require("../../assets/img/white1.png")} alt=""  className=" mx-auto d-block mb-5"/>
                        </div>
                        <h6 className="heading mb-3">Bitcoin Exchange Easified</h6>
                        <p className="desc mb-5">Easily lorem ipsum dolor esit amari. Instant payment, no stress, no story.</p>
                        <div className="d-flex justify-content-between button-container px-3">
                            <p className=" mb-0 px-4 py-2" onClick={() => this.props.setMenu("home")} style={{background:"#9E1C31", borderRadius:"5px"}}>Get Started</p>
                            <p className="button mb-0 px-5 py-2" onClick={this.next}>Next</p>
                        </div>
                        </div>
                    </div>
                    
                    </div>

                    <div className="sp splash-1 relative">
                    <div className="px-4 h-100 splash-bg d-flex align-items-center justify-content-center" style={{background:`linear-gradient(to right, rgba(0,0,0,.75), rgba(0,0,0,.75)), url(${require("../../assets/img/amb-2.JPG")})`, backgroundSize:"cover", backgroundPosition:"right" }}>
                        <div className="">
                        <div className="logo">
                            <img src={require("../../assets/img/white1.png")} alt=""  className=" mx-auto d-block mb-5"/>
                        </div>
                        <h6 className="heading mb-3">Bitcoin Exchange Easified</h6>
                        <p className="desc mb-5">Easily lorem ipsum dolor esit amari. Instant payment, no stress, no story.</p>
                        <div className="d-flex justify-content-between button-container px-3">
                            <p className=" mb-0 px-4 py-2" onClick={() => this.props.setMenu("home")} style={{background:"#9E1C31", borderRadius:"5px"}}>Get Started</p>
                            <p className="button mb-0 px-5 py-2"  onClick={() => this.props.setMenu("home")}>Next</p>
                        </div>
                        </div>
                    </div>
                    
                    </div>

                </Slider>
                </div>
            </UI.Wrapper>
        )
    }
}
