import styled from 'styled-components';

export const Wrapper = styled.div`
background:#f4d1a9;
min-height:100vh;
padding-bottom:100px;
h4{
    background: #f4d1a9;
    padding: 20px 20px;
    border-bottom-right-radius: 13px;
    border-bottom-left-radius: 10px;
}
.wallets{
    background:#fff;
    min-height:80vh;
    border-top-right-radius:8%;
    border-top-left-radius:8%;
    .wallet-intro{
        font-size:16px;
            letter-spacing:.5px;
    }

}
    .wallet{
        background: #f4d1a9;
        border-radius:15px;
        color:white;
        h4{
            color:#9E1C31;
        }
        .address{
            width:80%;
            overflow-x: hidden;
            text-overflow: ellipsis;
            color:#000;
        }
        }
        .copy{
            font-size:14px;
            color:#000;
            font-weight:500;
        }
    }
    .table{
        border-radius:10px;
    }
`;