import styled from 'styled-components';
// #F89521
export const Wrapper = styled.div`
   
    min-height:100vh;
    background:#f4f4fb8;
    padding-bottom:100px;
    
    .intro{
        padding-top:160px;
    }
    .welcome-card{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width:100%;
        overflow-x: hidden;

        .content{
            width:125%;
            height: 370px;
            border-radius: 50%;
            // background: linear-gradient(45deg,#ff9800c9,#af4c7659);
            background: linear-gradient(45deg,#9E1C31,#af4c7691);
            transform: translate(-10%,-50%);
        }

        img{
            position:absolute;
            bottom:30px;
            left:50%;
            transform: translate(-50%,0%);
            width:210px;

        }
    }
    .trade-box{
       h6{
           font-size:20px;
       }
        border-radius:15px;
        p{
            font-size:13px;
            letter-spacing:1px;
        }
    }
    
    @media (max-width:800px){
    }
    @media (max-width:500px){

    }
`;

export default Wrapper