import styled from 'styled-components';

export const Wrapper = styled.div`
    .splash-screen{
        height:100%;
        overflow:hidden;
        .sp{
            color:#fff;
            height:100vh;
    
            .heading{
                font-size:30px;
                font-family:Roboto, sans-serif;
                letter-spacing:1px;
                line-height:35px;
            }
            .desc{
                letter-spacing:1px;
                font-size:16px;  
                font-weight:300;
                line-height:26px;
            }
        }
        .logo{
            position:absolute;
            top:50px;
            right:0;
            left:0;
            img{
                height:35px;
            }
        }

        .button-container{
            font-size:14px;
            color:#fff;
            letter-spacing:1px;
        }
        .button{
            background:#f29629;
            color:#fff;
            border-radius:3px;
            box-shadow:1px 1px 5px rgba(255,255,255,.1);
            font-size:14px;
        }
    }
    
    @media (max-width:800px){
     
    }
    @media (max-width:500px){
       
    }
`;

export default Wrapper