import React from 'react';
import * as UI from './style';

export default function Index({menu, setMenu}) {
  // console.log(menu)
    return (
        <UI.Wrapper className="menu px-2 py-2">
            <ul className="m-0 p-0 d-flex justify-content-between">
              <li onClick={() =>setMenu("home")}>
                <div>
                  <div className="icon mx-auto" style={{background:`url(${menu === "home" ? require("../../assets/img/home.svg") : require("../../assets/img/home-grey.svg")}) no-repeat`}}></div>
                </div>
                <p className={`mb-0 ${menu === "home" ? "active" : ""}`}>Home</p>
              </li>
              <li onClick={() =>setMenu("rates")}>
                <div>
                  <div className="icon mx-auto" style={{background:`url(${menu === "rates" ? require("../../assets/img/rate.svg") : require("../../assets/img/rate-grey.svg")}) no-repeat`}}></div>
                </div>
                <p className={`mb-0 ${menu === "rates" ? "active" : ""}`}>Rates</p>
              </li>

              <li onClick={() =>setMenu("wallet")}>
                <div>
                  <div className="icon mx-auto" style={{background:`url(${menu === "wallet" ? require("../../assets/img/wallet.svg") : require("../../assets/img/wallet-grey.svg")}) no-repeat`}}></div>
                  {/* <img src={require("../../assets/img/home.svg")} alt=""  className=" mx-auto d-block mb-0"/> */}
                </div>
                <p className={`mb-0 ${menu === "wallet" ? "active" : ""}`}>Wallet</p>
              </li>

              <li onClick={() =>setMenu("card")}>
                <div>
                  <div className="icon mx-auto" style={{background:`url(${menu === "card" ? require("../../assets/img/gift-card.svg") : require("../../assets/img/gift-card-grey.svg")}) no-repeat`}}></div>
                  {/* <img src={require("../../assets/img/home.svg")} alt=""  className=" mx-auto d-block mb-0"/> */}
                </div>
                <p className={`mb-0 ${menu === "card" ? "active" : ""}`}>Cards</p>
              </li>
            </ul>
        </UI.Wrapper>
    )
}
